// src/contexts/DataContext.tsx
import React, { createContext, useContext, ReactNode, useState, useEffect } from 'react'; //  useEffect
import { useLocalStorage } from '../hooks/useLocalStorage';
import { AppData, Task } from '../types'; //  Task 
import { getTodayString, isTodayWeekday } from '../utils/dateUtils'; //  

// 1. 
const initialData: AppData = {
  projects: [],
  tasks: [],
  tags: [],
  settings: {
    theme: "dark",
    accentColor: "#2998ff",
  },
};

// 2. Context
// ()
interface DataContextType {
  appData: AppData;
  setAppData: React.Dispatch<React.SetStateAction<AppData>>;

  //  : ID state
  editingTaskId: string | null;
  setEditingTaskId: React.Dispatch<React.SetStateAction<string | null>>;
}

// 3. Context 
// ( Provider )
const DataContext = createContext<DataContextType | undefined>(undefined);

// 4. Provider
export const DataProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  // 5.  useLocalStorage 
  //  appData 
  const [appData, setAppData] = useLocalStorage<AppData>("todo-app-data", initialData);

  //  :  state
  // ()
  const [editingTaskId, setEditingTaskId] = useState<string | null>(null);

  //  : 
  useEffect(() => {
    const todayStr = getTodayString();
    const todayIsWeekday = isTodayWeekday();

    const tasksToReset = appData.tasks.filter(task => {
      // 1. 
      if (!task.repeatConfig) return false;
      // 2. 
      if (task.status !== 'Done') return false;
      // 3. 
      //    ()
      if (task.lastCompletedDate === todayStr) return false;

      // 4. 
      if (task.repeatConfig.type === 'daily') {
        return true; // 
      }
      if (task.repeatConfig.type === 'weekdays' && todayIsWeekday) {
        return true; // 
      }

      return false;
    });

    // 
    if (tasksToReset.length > 0) {
      const resetTaskIds = tasksToReset.map(t => t.id);

      setAppData(prevData => ({
        ...prevData,
        tasks: prevData.tasks.map(task =>
          // ID
          resetTaskIds.includes(task.id)
            // 
            ? { ...task, status: 'Todo', lastCompletedDate: null }
            : task // 
        ),
      }));
    }
    // appData.tasks 
    //  appData setAppData 
    // useLocalStorage  appData 
    // appData 
    // 1
    //    appData 
    //   
    //    appData.tasks 

    //  [appData.tasks, setAppData] 
    //       
    //       lastCompletedDate === todayStr
  }, [appData.tasks, setAppData]);


  return (
    //  : value  editingTaskId  setEditingTaskId 
    <DataContext.Provider value={{ appData, setAppData, editingTaskId, setEditingTaskId }}>
      {children}
    </DataContext.Provider>
  );
};

// 6. 
export const useData = () => {
  const context = useContext(DataContext);
  if (context === undefined) {
    throw new Error('useData must be used within a DataProvider');
  }
  return context;
};