// src/themes.ts

// styled-components  DefaultTheme 
export interface AppTheme {
  mode: 'light' | 'dark'; // 
  body: string;     // 
  panel: string;    // 
  item: string;     // 
  itemHover: string;// 
  text: string;     // 
  subText: string;  // 
  border: string;   // 
  accent: string;   // 
  
  //  : 
  success: string;  // 
  danger: string;   // 
}

//  (Tailwind CSSSlate)
const colors = {
  slate950: '#020617', // 
  slate900: '#0f172a', // 
  slate800: '#1e293b', // 
  slate700: '#334155', // 
  slate600: '#475569',
  slate400: '#94a3b8', // 
  slate200: '#e2e8f0', // 
  slate50:  '#f8fafc', // 
};

// 
export const lightThemeBase: Omit<AppTheme, 'accent'> = {
  mode: 'light',
  body: '#f1f5f9', // Slate-100
  panel: '#ffffff',
  item: '#ffffff',
  itemHover: '#f8fafc',
  text: '#0f172a',
  subText: '#64748b',
  border: '#e2e8f0',
  success: '#22c55e',
  danger: '#ef4444',
};

//  Silent Tech 
export const darkThemeBase: Omit<AppTheme, 'accent'> = {
  mode: 'dark',
  // 
  body: colors.slate950,  // 
  panel: colors.slate900, // 
  item: 'transparent',    // 
  itemHover: colors.slate800, // 
  text: colors.slate200,      // (#fff)
  subText: colors.slate400,   // 
  border: colors.slate800,    // 
  
  success: '#10b981', // Emerald-500 ()
  danger: '#f43f5e',  // Rose-500
};

// 
export const accentColors: { [key: string]: string } = {
  blue: '#3b82f6',   // Blue-500 ()
  cyan: '#06b6d4',   // Cyan-500 ()
  violet: '#8b5cf6', // Violet-500 ()
  orange: '#f97316', // Orange-500
};

export const createTheme = (mode: 'dark' | 'light', accentName: string): AppTheme => {
  const baseTheme = mode === 'dark' ? darkThemeBase : lightThemeBase;
  const accentColor = accentColors[accentName] || accentColors.blue;
  
  return {
    ...baseTheme,
    accent: accentColor,
  };
};