// src/App.tsx
import React from 'react';
import styled, { ThemeProvider, createGlobalStyle } from 'styled-components'; // 
import { BrowserRouter, Routes, Route } from 'react-router-dom';

import { TodayView } from './views/TodayView';
import { AllProjectsView } from './views/AllProjectsView';
import { TagsView } from './views/TagsView';
import { SettingsView } from './views/SettingsView';
import { BottomNavigation } from './components/BottomNavigation';
import { DataProvider, useData } from './contexts/DataContext'; //  useData 
import { TaskDetailModal } from './components/TaskDetailModal';
import { createTheme } from './themes'; //  createTheme 

//  : 
// (props.theme )
const GlobalStyle = createGlobalStyle`
  body {
    background-color: ${props => props.theme.body};
    color: ${props => props.theme.text};
    /*  */
    transition: background-color 0.2s ease, color 0.2s ease;
  }
`;

const MainContent = styled.main`
  padding: 16px;
  padding-bottom: 80px;
`;

//  : App.tsx  AppContent 
// (useData()  ThemeProvider )
const AppContent: React.FC = () => {
  // useData theme, accentColor
  const { appData } = useData();
  const { theme: themeMode, accentColor } = appData.settings;
  
  // 
  const activeTheme = createTheme(themeMode, accentColor);

  return (
    //  : ThemeProvider 
    <ThemeProvider theme={activeTheme}>
      <GlobalStyle /> {/*   */}
      <div>
        <header>
          {/*  */}
        </header>

        <MainContent>
          <Routes>
            <Route path="/" element={<TodayView />} />
            <Route path="/projects" element={<AllProjectsView />} />
            <Route path="/tags" element={<TagsView />} />
            <Route path="/settings" element={<SettingsView />} />
          </Routes>
        </MainContent>

        <BottomNavigation />
        <TaskDetailModal />
      </div>
    </ThemeProvider>
  );
};

//  : App Provider 
function App() {
  return (
    <BrowserRouter>
      <DataProvider>
        {/*  AppContent  */}
        <AppContent />
      </DataProvider>
    </BrowserRouter>
  );
}

export default App;